.popup__input {
  font-family: inherit;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  width: 358px;
  height: 27px;
  outline: none;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  /* margin-top: 30px; */
}

.popup__input:focus {
  outline: none;
}

@media screen and (max-width: 690px) {
  .popup__input {
    font-size: 14px;
    line-height: 17px;
    width: 83%;
    margin-bottom: 24px;
  }
}

@media screen and (max-width: 450px) {
  .popup__input {
    margin-bottom: 20px;
  }
}
